import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

let lifeData = {};

try {
	// lifeDataAPP
	lifeData = uni.getStorageSync('lifeData');
} catch (e) {

}

// APPstate
let saveStateKeys = ['vuex_user', 'vuex_token'];

// 
const saveLifeData = function(key, value) {
	// 
	if (saveStateKeys.indexOf(key) != -1) {
		// lifeData
		let tmp = uni.getStorageSync('lifeData');
		// APPlifeData{}
		tmp = tmp ? tmp : {};
		tmp[key] = value;
		// lifeData
		uni.setStorageSync('lifeData', tmp);
	}
}
const store = new Vuex.Store({
	state: {
		// lifeDatastate
		// vuex_
		vuex_user: lifeData.vuex_user ? lifeData.vuex_user : {
			name: ''
		},
		vuex_token: lifeData.vuex_token ? lifeData.vuex_token : '',
		// vuex_versionlifeData.vuex_version
		vuex_version: '1.0.1',
		vuex_demo: '',

		hasLogin: false,
		userName: "",
		userType: "",
		userId: '',
		userDepartmentName: '',
		userDepartmentGuid: '',
		roleName: '',
		token: '',
		seltab: 0,
		seltit: '',
		phone: '',
		openid: '',
		shopid: '',
		HomeAddressUUID: '',
		idCard: '',
    // tabbar
		// vuex_tabbar: [{
		// 		iconPath: "/static/uview/example/component.png",
		// 		selectedIconPath: "/static/uview/example/component_select.png",
		// 		text: '',
		// 		pagePath: '/pages/example/components'
		// 	},
		// 	{
		// 		iconPath: "/static/uview/example/js.png",
		// 		selectedIconPath: "/static/uview/example/js_select.png",
		// 		text: '',
		// 		midButton: true,
		// 		pagePath: '/pages/example/js'
		// 	},
		// 	{
		// 		iconPath: "/static/uview/example/template.png",
		// 		selectedIconPath: "/static/uview/example/template_select.png",
		// 		text: '',
		// 		pagePath: '/pages/example/template'
		// 	}
		// ]
	},
	mutations: {
		$uStore(state, payload) {
			// stateuser.info.score = 1
			let nameArr = payload.name.split('.');
			let saveKey = '';
			let len = nameArr.length;
			if (len >= 2) {
				let obj = state[nameArr[0]];
				for (let i = 1; i < len - 1; i++) {
					obj = obj[nameArr[i]];
				}
				obj[nameArr[len - 1]] = payload.value;
				saveKey = nameArr[0];
			} else {
				// state
				state[payload.name] = payload.value;
				saveKey = payload.name;
			}
			// 
			saveLifeData(saveKey, state[saveKey])
		},
		login: function(state, user, phone) {
			console.log(user);
			state.userName = user.userName;
			state.hasLogin = true;
			state.userType = user.userType;
			state.userId = user.userId;
			//state.userDepartmentName=user.DepartmentName;
			//state.userDepartmentGuid=user.DepartmentGuid;
			state.roleName = user.RoleName;
			state.token = user.token;
			state.phone = user.phone;
			state.openid = user.openid;
			// state.EWM = "";
			// state.EWMShowCount = true;
			// state.shopid = user.shopid;
			// state.HomeAddressUUID = user.HomeAddressUUID;
			state.idCard = user.idCard;
		},
		logout(state) {
			state.userName = "";
			state.hasLogin = false;
			state.userId = '';
			state.userType = '';
			// state.userDepartmentName = '';
			// state.userDepartmentGuid = '';
			state.roleName = '';
			state.token = '';
			state.phone = '';
			// state.shopid = '';
			// state.HomeAddressUUID = '';
			//state.openid='';
			state.idCard = '';
		}
	}
})

export default store
