import http from '@/utils/http.js'
//
export const getStrategyList = (data) => {
	return http.httpRequest({
		url: 'api/v1/wx/traveltips/strategy/list',
		method: 'post'
	},data)
}
export const getStrategyList2 = (data) => {
	return http.httpRequest({
		url: 'api/v1/wx/traveltips/strategy/list2',
		method: 'post'
	},data)
}
